/* ---------- Animations ---------- */

@keyframes top-to-bottom {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }

  50% {
    opacity: 0.6;
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bottom-to-top {
  from {
    opacity: 0;
    transform: translateY(150px);
  }

  50% {
    opacity: 0.6;
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes left-to-right {
  from {
    opacity: 0;
    transform: translateX(-250px);
  }

  50% {
    opacity: 0.6;
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

// Icon animations

@keyframes pulsing {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.5, 1.5);
  }
  100% {
    transform: scale(1, 1);
  }
}

// Product cards animation

@keyframes product-image-pulsing {
  0% {
    transform: translateY(-110%) scale(1, 1);
  }
  50% {
    transform: translateY(-110%) scale(1.5, 1.5);
  }
  100% {
    transform: translateY(-110%) scale(1, 1);
  }
}

// Анімація зміни зображень у секції "Gallery"
@keyframes ice-cream {
	0% {
	  opacity:1;
	}
	17% {
	  opacity:1;
	}
	25% {
	  opacity:0;
	}
	92% {
	  opacity:0;
	}
	100% {
	  opacity:1;
	}
}
  